.radio {
    position: fixed;
    top: -1.5em;
    left: -1.5em;
}

.radio-label {
    display: block;
    font-size: 16px;
    line-height: 1.5;
    color: #000;
    cursor: pointer;
    transition: color 0.2s cubic-bezier(0.45, 0.05, 0.55, 0.95);
  }
  .radio-label:not(:last-of-type) {
    margin-bottom: 1.5em;
  }
  .radio-label span {
    // box-shadow: 0 0 0 0.1em currentColor inset, 0 0.1em 0.1em rgba(0, 0, 0, 0.2), 0 0.2em 0.1em rgba(0, 0, 0, 0.2) inset;
    display: inline-block;
    margin-right: 30px;
    vertical-align: bottom;
    width: 1.5em;
		height: 1.5em;
    border: 1px solid #000;
    transition: transform 0.2s cubic-bezier(0.5, 0, 0.5, 2), box-shadow 0.2s cubic-bezier(0.45, 0.05, 0.55, 0.95), color 0.2s cubic-bezier(0.45, 0.05, 0.55, 0.95);
  }

  
  .radio-label span,
  .worm__segment:before {
    border-radius: 50%;
  }
  
  .radio:checked + .radio-label,
  .radio:checked + .radio-label span,
  .worm__segment:before {
    color: $green;
  }
  
  .radio:checked + .radio-label, .radio:checked + .radio-label span {
    transition-delay: 0.4s;
  }
  .radio:checked + .radio-label span {
    transform: scale(1.2);
  }
  
  .worm {
    top: 0.375em;
    left: 0.375em;
  }
  .worm, .worm__segment {
    position: absolute;
  }
  .worm__segment {
    top: 1px;
    left: 2px;
    width: 0.75em;
		height: 0.75em;
    transition: transform 0.4s cubic-bezier(0.45, 0.05, 0.55, 0.95);
  }
  .worm__segment:before {
    animation-duration: 0.4s;
    animation-timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95);
    background: currentColor;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
  }
   
