  /* States */
  .radio:nth-of-type(1):checked ~ .worm .worm__segment:before {
    animation-name: hop1;
  }
  
  @keyframes hop1 {
    from, to {
      transform: translateX(0);
    }
    50% {
      transform: translateX(-1.5em);
    }
  }
  .radio:nth-of-type(2):checked ~ .worm .worm__segment {
